import { Component, Input, OnInit, SimpleChanges, DoCheck, ElementRef, AfterViewInit, Renderer2, NgZone } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { DashboardService } from './dashboard.service';

@Component({
    selector: 'ux-dashboard',
    template: require('./dashboard.component.html'),
    styles: [require('./dashboard.component.less')],
    providers: [ DashboardService ]
})
export class DashboardComponent implements OnInit, DoCheck, AfterViewInit {

    @Input() options: DashboardOptions = {};

    private _nativeElement: HTMLElement;
    private _options: DashboardOptions;

    constructor(private _dashboardService: DashboardService, private _elementRef: ElementRef, private _renderer: Renderer2, private _ngZone: NgZone) {
        this._nativeElement = _elementRef.nativeElement;
        this._dashboardService.setDashboard(this._nativeElement);

        // watch for changes to component height
        this._dashboardService.height.subscribe(height => {
            this._renderer.setStyle(this._elementRef.nativeElement, 'height', `${height}px`);
        });
    }

    ngOnInit() {
        this.setOptions(this.options);
    }

    ngDoCheck(): void {
        
        // get the current set of options
        let options = Object.assign({}, this._dashboardService.getDefaultOptions(), this.options);

        // if anything has changed then update them
        if (this._dashboardService.getOptions() !== options) {
            this.setOptions(options);
        }
    }

    ngAfterViewInit() {
        // initially set dimensions
        this._dashboardService.setDimensions(this._nativeElement.offsetWidth, this._nativeElement.offsetHeight);
    }

    setOptions(options: DashboardOptions) {
        this._dashboardService.setOptions(options);
    }

    onResize(event: Event) {
        // ensure this gets run inside Angular
        this._ngZone.run(() => {
            let target = event.target as Window;
            this._dashboardService.setDimensions(target.innerWidth, target.innerHeight);
        });
    }
}

export interface DashboardOptions {
    columns?: number;
    padding?: number;
    minWidth?: number;
    minHeight?: number;
    rowHeight?: number;
    maxRows?: number;
}


